        &[data-type="6"] {
            background-image: url("../images/sprites/blue_6.png"); }

        &[data-type="7"] {
            background-image: url("../images/sprites/blue_7.png"); }

        &[data-type="8"] {
            background-image: url("../images/sprites/blue_8.png"); }

        &[data-type="9"] {
            background-image: url("../images/sprites/blue_9.png"); }

        &[data-type="10"] {
            background-image: url("../images/sprites/blue_10.png"); }

        &[data-type="flag"] {
            background-image: url("../images/sprites/blue_flag.png"); }

        &[data-type="bomb"] {
