@import "board";

body {
    header {
        padding: 0px 100px;
        height: 80px;
        box-shadow: 0px 0px 3px #888;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .logo {
            background: url("../images/logo3.png");
            background-size: contain;
            background-repeat: no-repeat;
            height: 100%;
            width: 300px; } }

    .stage-container {
        position: relative;
        height: 100vh;

        .back-wrapper {
            position: absolute;
            top: 20px;
            left: 20px;
            width: 100px;
            height: 50px; }
        .stage {
            width: 100vw;
            box-sizing: border-box;
            opacity: 0;
            position: absolute;
            top: 80px;
            bottom: 0px;
            transition: opacity 150ms linear;
            animation: zoomIn 150ms linear forwards;

            @keyframes zoomIn {
                0% {
                    opacity: 0;
                    transform: scale(5); }

                100% {
                    opacity: 1;
                    transform: scale(1); } } } } }




#stage-1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .login-wrapper {


        .login-container {
            // width: 800px
            // display: flex
            // flex-direction: row

            .side {
                min-width: 450px;
                min-height: 300px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: center;
                padding: 50px; } } } }



#stage-2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
        font-size: 3rem; }

    .ui.segment {
        display: flex;
        flex-direction: column;
        align-items: center;

        .action.input {
            margin-bottom: 20px; } } }

#stage-4,
#stage-3 {
    .stage-3-grid {
        padding-top: 20px; }

    h1,h2,h3,p {
        text-align: center; }

    .comments {
        padding: 0 20px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .comment {
            width: 80%;
            // background-color: #69696946
            box-shadow: 0px 0px 2px #999;
            border-radius: 3px;
            transition: box-shadow 100ms linear;

            .avatar img {
                height: 50px;
                width: 50px;
                margin-right: 30px; }

            &[data-ready="true"] {
                box-shadow: 0px 0px 2px #999, 0px 0px 2px 2px #5cfc00fd inset; } } }


    .ready-button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%; } }

.list.custom {
    width: fit-content;
    margin: 40px auto !important;
    .item {
        border-radius: .28571429rem;
        margin: 10px;
        &.active {
            border: 1px solid #21ba45 !important; } } }
